<template>
    <div class="vh-animate vh-animate-fade-in">
        <h1 class="axs-title vh-text-center vh-margin-remove">
            DASHBOARD
        </h1>

        <div class="vh-margin-top">
            <div v-if="games.length">
                <ul class="vh-list vh-list-divider vh-list-hover vh-card-scroll">

                    <item-component
                        v-for="(item, index) in games"
                        :key="index"
                        :item="item"/>

                </ul>

                <div class="vh-margin-small-top vh-text-center">
                    <router-link
                        class="vh-button vh-button-default vh-button-large vh-button-full-rounded"
                        :to="'/game'">
                    ðŸš€ PLAY
                    </router-link>
                </div>
            </div>
            
            <div class="vh-text-center vh-margin-top" v-else>
                <div class="vh-text-center">
                    <img src="static/assets/images/empty.png" width="300" height="300">
                </div>

                <router-link
                    class="vh-button vh-button-default vh-button-large vh-button-full-rounded"
                    :to="'/game'">
                   ðŸš€ PLAY
                </router-link>
            </div>
        </div>

        <show-modal v-if="hasSelected" />
        
    </div>
</template>

<script>
import { mapGetters } from 'vuex';

import ItemComponent from './content/item';
import ShowModal from './components/modals/show';

export default
{
    name: 'Dashboard',

    components:
    {
        ItemComponent,

        ShowModal
    },

    data()
    {
        return {
            selected: {},

            showGameModal: false,
        };
    },

    computed:
    {
        ...mapGetters({
            games: 'auth/games'
        }),

        hasSelected()
        {
            return Object.keys(this.selected).length > 0;
        },
    },

    methods:
    {
        toggleModal(name, bool)
        {
            this[`${name}Modal`] = bool;
        },
    }
};
</script>



