<template>
    <vh-modal
        :isOpen.sync="$parent.$data.showGameModal"
        :isCenter="true">

        <vh-modal-body>
            <div class="vh-margin-top">
                <div class="vh-text-center">
                    <h1 class="vh-text-title vh-margin-remove-bottom">
                        {{selected.name}}
                    </h1>
                    <small >
                        <vh-moment :datetime="selected.created" />
                    </small>
                </div>
                
                <ul class="vh-list vh-list-divider vh-margin-medium-top vh-visible@m">
                    <li>
                        <vh-grid
                            :gutter="'small'">
                            <div class="vh-width-1-1@s vh-width-1-4@m vh-width-1-4@l vh-text-center">
                                <small class="vh-text-bold vh-text-center">
                                    ROUND
                                </small>
                            </div>
                            <div class="vh-width-1-1@s vh-width-1-4@m vh-width-1-4@l vh-text-center">
                                <small class="vh-text-bold">
                                    USED
                                </small>
                            </div>
                            <div class="vh-width-1-1@s vh-width-1-4@m vh-width-1-4@l vh-text-center">
                                <small class="vh-text-bold">
                                    GAINED
                                </small>
                            </div>
                            <div class="vh-width-1-1@s vh-width-1-4@m vh-width-1-4@l vh-text-center">
                                <small class="vh-text-bold">
                                    STOLEN
                                </small>
                            </div>
                        </vh-grid>
                    </li>
                    <li v-for="(round, index) in selected.rounds"
                        :key="index">
                        <vh-grid
                            :gutter="'small'">
                            <div class="vh-width-1-1@s vh-width-1-4@m vh-width-1-4@l vh-text-center">
                                <p class="vh-text-bold vh-margin-remove">
                                    {{round.roundNumber}}
                                </p>
                            </div>
                            <div class="vh-width-1-1@s vh-width-1-4@m vh-width-1-4@l vh-text-center">
                                <p class="vh-text-bold vh-margin-remove">
                                    {{round.usedEnergy}}
                                </p>
                            </div>
                            <div class="vh-width-1-1@s vh-width-1-4@m vh-width-1-4@l vh-text-center">
                                <p class="vh-text-bold vh-margin-remove">
                                    {{round.gainedEnergy}}
                                </p>
                            </div>
                            <div class="vh-width-1-1@s vh-width-1-4@m vh-width-1-4@l vh-text-center">
                                <p class="vh-text-bold vh-margin-remove">
                                    {{round.stolenEnergy}}
                                </p>
                            </div>
                        </vh-grid>

                    </li>
                </ul>

                <ul class="vh-list vh-list-divider vh-margin-medium-top vh-hidden@m">
                    <li v-for="(round, index) in selected.rounds"
                        :key="index">

                        <p class="vh-text-bold vh-text-center">
                            ROUND {{round.roundNumber}}
                        </p>
                        <vh-grid :gutter="'small'">
                            <div class="vh-width-expand">
                                <small class="vh-text-bold">
                                    USED ENERGY
                                </small>
                            </div>
                            <div class="vh-text-right">
                                <p class="vh-text-bold vh-margin-remove">
                                    {{round.usedEnergy}}
                                </p>
                            </div>
                        </vh-grid>
                        <vh-grid :gutter="'small'">
                            <div class="vh-width-expand">
                                <small class="vh-text-bold">
                                    GAINED ENERGY
                                </small>
                            </div>
                            <div class="vh-text-right">
                                <p class="vh-text-bold vh-margin-remove">
                                    {{round.gainedEnergy}}
                                </p>
                            </div>
                        </vh-grid>
                        <vh-grid :gutter="'small'">
                            <div class="vh-width-expand">
                                <small class="vh-text-bold">
                                    STOLEN ENERGY
                                </small>
                            </div>
                            <div class="vh-text-right">
                                <p class="vh-text-bold vh-margin-remove">
                                    {{round.stolenEnergy}}
                                </p>
                            </div>
                        </vh-grid>
                    </li>
                </ul>


                <hr>


                <div class="vh-margin-top vh-text-center">
                    <small class="vh-text-bold">
                        YOU {{selected.result == 'WIN' ? 'WIN':'LOSE'}}
                    </small>
                    <h1 class="vh-margin-remove">
                        <span v-if="selected.result == 'WIN'">
                            ðŸ‘‘
                        </span>
                        <span v-else>
                            ðŸ¤¬
                        </span>
                    </h1>
                </div>
                
                <div class="vh-margin-top vh-text-center">
                    <vh-button
                        class="vh-margin-xsmall-top"
                        type="button"
                        :size="'large'"
                        :outline="true"
                        @click="remove()"
                        :color="'danger'">
                        DELETE
                    </vh-button>
                </div>
                
            </div>
        </vh-modal-body>
    </vh-modal>
</template>

<script>
import { mapActions } from 'vuex';

import { Toast } from '@/utils';

export default
{
    computed:
    {
        selected()
        {
            return this.$parent.$data.selected;
        },
    },

    methods:
    {
        ...mapActions({
            removeGame: 'auth/removeGame'
        }),

        async remove()
        {
            await this.removeGame(this.$parent.$data.selected);

            this.closeModal();

            Toast.success({
                message: 'ðŸ¤Ÿ Game has been deleted.'
            });
        },

        closeModal()
        {
            this.$parent.toggleModal('showGame', false);
        },
    },
};
</script>



