<template>
    <li class="vh-point"
        @click="openModal('showGame')">
        <div class="vh-card vh-card-body-vh-padding-small">
            <vh-grid
                :gutter="'small'">
                <div class="vh-width-expand">
                    <h3 class="axs-game-name vh-text-bold vh-margin-remove">
                        {{item.name}}
                    </h3>
                    <p class="vh-margin-remove">
                        {{item.rounds.length}} {{ item.rounds.length > 1 ? 'rounds' : 'round'}}
                    </p>
                    <small class="vh-text-italic">
                        <vh-moment :datetime="item.created" />
                    </small>
                </div>
                <div class="vh-text-right">
                    <h2 class="vh-margin-remove">
                        <span v-if="item.result == 'WIN'">
                            ðŸ‘‘
                        </span>
                        <span v-else>
                            ðŸ¤¬
                        </span>
                    </h2>
                </div>
            </vh-grid>

            <div class="vh-text-right">
                
            </div>
            
        </div>
    </li>
</template>

<script>

export default
{
    props:
    {
        item: Object
    },

    methods:
    {
        openModal(name)
        {
            this.$parent.$data.selected = this.item;

            this.$parent.toggleModal(name, true);
        },
    },
};
</script>



